import React, { Component } from 'react'
import { disp1,disp2 } from './Game'
let kk = disp1,kkk = disp2;
export default class Preview extends Component {
    onPlayClick(e)
    {
        e.preventDefault();
        kk = "block";
        kkk = "none";
    }
    render() {
        return (
            <div style={{display: kkk}} className="preview">
             <div className="menu">
                <div>
                    <button onClick={this.onPlayClick} className="preview-buttons">Играть</button>
               </div>
               <div>
                    <button className="preview-buttons">Настройки</button>
               </div>
               <div>
                    <button className="preview-buttons">Играть</button>
               </div>
            </div> 
            </div>
        )
    }
}
